<template>
  <div class="loginbox" v-if="isShow">
    <div class="login">
      <h2>
        <span>基于Docker容器网络系统</span>
      </h2>
      <form class="form">
        <h3 class="h3">{{ $t('Login_Login') }}</h3>
        <p class="title">{{ $t('Login_Username') }}：</p>
        <p>
          <input @keyup.13="login" type="text" v-model.trim="username" />
        </p>
        <p class="title">{{ $t('Login_Password') }}：</p>
        <p>
          <input @keyup.13="login" type="password" autocomplete='off' v-model.trim="password" />
        </p>
        <p style="cursor:pointer;text-align: right;padding: 10px 0" @click="register">{{ $t('Login_Register') }}</p>
        <p class="submit">
          <el-button  @click="login">{{ $t('Login_Login') }}</el-button>
        </p>
      </form>
    </div>
    <div class="bottom">
      <myFooter />
    </div>
  </div>
</template>

<script>
import http from '@/utils/http'
import { Button } from 'element-ui'
import Vue from 'vue'
Vue.use(Button)
export default {
  data () {
    return {
      username: '',
      password: '',
      isShow: 1
    }
  },
  methods: {
    register(){
      this.$router.push('Regist')
    },
    login () {
      // 登录
      http({
        url: '/login',
        method: 'POST',
        timeout: 2000000,
        data: { username: this.username, password: this.password }
      }).then(res => {
        if (res.Code === 200) {
          this.$message({
            showClose: true,
            message: this.$t('Login_Message'),
            type: 'success'
          })
          const baseURL = localStorage.getItem('baseURL')
          const user = { username: this.username, token: res.Data, baseURL: baseURL }
          localStorage.setItem('user', JSON.stringify(user))
          localStorage.setItem('token', res.Data)
          localStorage.setItem('userList', JSON.stringify([user]))
          if (localStorage.getItem('user')) {
            this.$router.replace('/Home')
          }
        } else {
          return this.$message({
            showClose: true,
            message: res.Msg,
            type: 'error'
          })
        }
      })
    }
  }
}
</script>
